.footer {
  color: #fff;
  display: flex;
  justify-content: center;

  ul {
    display: flex;
  }

  ul li {
    transition: ease-in-out 0.2s;
  }

  ul li:hover {
    transform: scale(1.5);
  }

  a {
    color: #fff;
    font-style: normal;
  }

  img {
    width: 48px;
  }

  .container {
    transition: all 0.2s;

    .forMore {
      display: flex;
      justify-content: space-between;

      .bottomNav ul {
        display: flex;
      }
    }

    .address {
      .socialIcon-phone {
        display: none;
      }
      .phone,
      .email {
        line-height: 54px;

        a {
          margin: 16px;
        }

        a:hover {
          border-bottom: 1px solid #fff;
        }

        .material-symbols-outlined {
          transform: scale(2);
        }
      }
    }

    hr {
      border-top: 1px solid #fff;
    }

    .copyright {
      display: flex;
      justify-content: space-between;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    .container {
      .forMore {
        padding-top: 24px;
      }
      .bottomNav,
      .socialIcon {
        display: none;
      }

      .address {
        font-size: 16px;

        .phone-size {
          display: flex;
          justify-content: space-between;
          align-items: center;

          .socialIcon-phone {
            display: block;
            img {
              width: 24px;
            }
          }
        }
        .phone,
        .email {
          line-height: 24px;

          a {
            margin: 8px;
          }
        }
        .material-symbols-outlined {
          transform: scale(1) !important;
        }
      }

      hr {
        margin: 20px 0;
      }

      .copyright {
        display: block;
        padding-bottom: 24px;
        ul {
          display: block;
          li a {
            padding: 0;
          }
          li:hover {
            transform: scale(1.2);
          }
        }
      }
    }
  }
}
