.header {
  display: flex;
  justify-content: space-around;
  align-items: center; }
  .header .logo {
    transition: all 0.2s; }
    .header .logo img {
      width: 80px; }
  .header .logo:hover {
    transform: scale(1.2); }
  .header ul li:hover {
    transform: scale(1.2); }
  .header ul li {
    cursor: pointer;
    transition: all 0.2s; }
  .header .navBar {
    display: flex; }
    .header .navBar a {
      color: #fff; }

@media screen and (max-width: 768px) {
  .header {
    flex-wrap: wrap;
    padding-bottom: 0; }
    .header .logo {
      display: flex;
      justify-content: center;
      flex: 100%; }
    .header nav {
      width: 100%; }
    .header .navBar {
      display: grid;
      grid-template-columns: repeat(2, 1fr); }
      .header .navBar li {
        font-size: 16px;
        line-height: 1.5;
        text-align: center;
        border: 1px solid #fff;
        padding: 12px 0; } }

.footer {
  color: #fff;
  display: flex;
  justify-content: center; }
  .footer ul {
    display: flex; }
  .footer ul li {
    transition: ease-in-out 0.2s; }
  .footer ul li:hover {
    transform: scale(1.5); }
  .footer a {
    color: #fff;
    font-style: normal; }
  .footer img {
    width: 48px; }
  .footer .container {
    transition: all 0.2s; }
    .footer .container .forMore {
      display: flex;
      justify-content: space-between; }
      .footer .container .forMore .bottomNav ul {
        display: flex; }
    .footer .container .address .socialIcon-phone {
      display: none; }
    .footer .container .address .phone,
    .footer .container .address .email {
      line-height: 54px; }
      .footer .container .address .phone a,
      .footer .container .address .email a {
        margin: 16px; }
      .footer .container .address .phone a:hover,
      .footer .container .address .email a:hover {
        border-bottom: 1px solid #fff; }
      .footer .container .address .phone .material-symbols-outlined,
      .footer .container .address .email .material-symbols-outlined {
        transform: scale(2); }
    .footer .container hr {
