        .footer .container .address .phone-size .socialIcon-phone img {
          width: 24px; }
    .footer .container .address .phone,
    .footer .container .address .email {
      line-height: 24px; }
      .footer .container .address .phone a,
      .footer .container .address .email a {
        margin: 8px; }
    .footer .container .address .material-symbols-outlined {
