.flex-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.text-center {
  line-height: 1.5;
  text-align: center;
}

.section1 {
  height: calc(100vh - 102px);
  background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/home-header.png);
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  padding-right: 16.125vw;

  .slogan {
    text-align: end;
    line-height: 1.5;
    font-weight: bolder;
  }

  .buyNow {
    background-color: $color1;
    text-align: 40px;
    a {
      color: #fff;
    }
  }
}

.section2 {
  .material-symbols-outlined {
    transform: scale(3.475);
  }

  ul {
    li p {
      max-width: 306px;
    }

    .fz-4 {
      text-align: left;
      height: 72px;
    }
  }
}

.section3 {
  .flex-box::after {
    content: " ";
    width: 416px;
  }

  ul {
    img {
      width: 416px;
    }
  }
}

.section4 {
  background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/index-bg-md.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  img {
    width: 648px;
  }
}

.recommend {
  ul li {
    height: 508px;
    text-align: left;
    border: 1px solid #dee2e6;
    border-radius: 5px;
    box-shadow: 0 0 10px 2px #dee2e6;
    position: relative;

    .date {
      font-size: 14px;
      position: absolute;
      bottom: 16px;
    }
  }

  img {
    width: 306px;
  }

  p {
    width: 282px;
  }
}

.contect {
  padding: 0 16.15vw;
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    text-align: left;
    max-width: 635px;
  }

  form {
    text-align: left;
    max-width: 635px;

    input:not(.submit, .checkbox),
    textarea {
      padding: 6px 2px;
      width: 100%;
      background-color: $color2;
      border: 3px solid #000;
      color: #000;
    }

    .checkbox {
      vertical-align: baseline;
      width: 30px;
      height: 16px;
    }

    .submit {
      background-color: $color1;
      color: #fff;
      border: none;
      width: 80%;
      height: 56px;
      margin: 48px 10% 0 10%;
      cursor: pointer;
    }

    .submit:active {
      border: 2px solid white;
    }
  }
}

.hover {
  transition: ease-in-out 0.1s;
}

.hover:hover {
  transform: scale(1.05);
}
