.header {
  display: flex;
  justify-content: space-around;
  align-items: center;

  .logo {
    img {
      width: 80px;
    }
    transition: all 0.2s;
  }

  .logo:hover {
    transform: scale(1.2);
  }

  ul li:hover {
    transform: scale(1.2);
  }

  ul li {
    cursor: pointer;
    transition: all 0.2s;
  }

  .navBar {
    display: flex;

    a {
      color: #fff;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    flex-wrap: wrap;
    padding-bottom: 0;

    .logo {
      display: flex;
      justify-content: center;
      flex: 100%;
    }

    nav {
      width: 100%;
    }

    .navBar {
      display: grid;
      grid-template-columns: repeat(2, 1fr);

      li {
        font-size: 16px;
        line-height: 1.5;
        text-align: center;
        border: 1px solid #fff;
        padding: 12px 0;
      }
    }
  }
}
